@import url("https://use.typekit.net/ufa2ftq.css");

.pokemon-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.poke_div {
    margin: 10px;
    width: 236px;
    height: 280px;
    border-radius: 5px;
    // box-shadow: 0 2px 30px rgba(black, 0.2);
    // background: rgb(10, 0, 0);
    position: relative;
    overflow: hidden;
    &:hover{
        cursor: pointer;
        & > .wave{
            animation: drift 4500ms infinite linear;
        }
        & > .wave.-three{
            animation: drift 7500ms infinite linear;
        }
        & > .wave.-two{
            animation: drift 10500ms infinite linear;
        }
        & > .poke_text{
            color: #ff0100;
        }
        & > .poke_img{
            transition: 0.5s;
            transform: scale(1.1);
        }
    }
}

.wave {
    opacity: 0.4;
    position: absolute;
    top: 3%;
    left: 50%;
    width: 450px;
    height: 450px;
    margin-left: -225px;
    margin-top: -225px;
    transform-origin: 50% 48%;
    border-radius: 43%;
    &:not(.-four){
        background: #950101;
    }
}


.wave.-two {
    opacity: 0.1;
    background: rgb(255, 255, 255);
}


.wave.-four{
    background: radial-gradient(at bottom, rgba(#ff0100, 1), rgba(#000, 0));
}

.poke_div:after {
    content: "";
    display: block;
    left: 0;
    top: 0;
    width: 236px;
    height: 280px;
    border-radius: 5px;
    // background: linear-gradient(to bottom, rgba(#950101, 1), rgba(#950101, 0) 80%, rgba(rgb(10, 0, 0), 0));
    z-index: 11;
}

.poke_img {
    transition: 0.5s;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 200px;
    z-index: 1;
    display: flex;
    justify-content: center;
    img{
        height: 200px;
        width: auto;
    }
}

.poke_text{
    position: absolute;
    bottom: 5px;
    left: 50%;
    width: 100%;
    transform: translateX(-50%);
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    color: rgb(255, 255, 255);
    font-family: open-sans, sans-serif;
    font-weight: 700;
    font-style: normal;
    font-size: 18px;
}

.notFound{
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    color: rgb(255, 255, 255);
    font-family: open-sans, sans-serif;
    font-weight: 800;
    font-style: normal;
    font-size: 36px;
}

.spin{
    height: 50px;
    width: 50px;
    border: 2px solid #ff0100;
    border-bottom: none;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

.buttons{
    padding: 20px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    .button{
        button{
            display: flex;
            align-items: center;
            justify-content: center;
            height: 42px;
            width: 144px;
            border: none;
            outline: none;
            background: #950101;
            color: white;
            font-family: open-sans, sans-serif;
            font-weight: 500;
            font-style: normal;
            font-size: 18px;
            border-radius: 18px;
            cursor: pointer;
            &:hover{
                background: #ff0100;
            }
            span{
                margin: 10px;
            }
        }
    }
}

@keyframes drift {
    from {
        transform: rotate(0deg);
    }
    from {
        transform: rotate(360deg);
    }
}

@keyframes spin{
    from{
        transform: rotate(0);
    }
    to{
        transform: rotate(360deg);
    }
}