@mixin listStyle {
    position: relative;
    left: 20px;
    li{
        list-style: none;
        position: relative;
        &:before{
            content: "";
            position: absolute;
            left: -20px;
            top: 8px;
            height: 3px;
            width: 3px;
            background: none;
            border-radius: 50%;
            border: 2px solid #ff0100;
        }
    }
}

.pokemon-stats-background{
    position: fixed;
    left: 0;
    top: 0;
    height: 100vh;
    width: 100vw;
    background: transparent;
    z-index: 11;
    cursor: default;
    display: none;
}

.pokemon-stats{
    position: relative;
    padding: 10px;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    height: 500px;
    width: 800px;
    background: rgb(10, 10, 10);
    z-index: 1;
    cursor: default;
    @media screen and (max-width: 850px) {
        transform: translate(-50%, -50%) scale(0.9);
    }
    @media screen and (max-width: 780px) {
        transform: translate(-50%, -50%) scale(0.8);
    }
    @media screen and (max-width: 700px) {
        transform: translate(-50%, -50%) scale(0.7);
    }
    @media screen and (max-width: 600px) {
        transform: translate(-50%, -50%) scale(0.6);
    }
    @media screen and (max-width: 500px) {
        transform: translate(-50%, -50%) scale(1);
        width: 250px;
        height: 450px;
    }
}

.background{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: #ff0100;
}

.line-1, .line-2{
    display: flex;
    font-family: open-sans, sans-serif;
    text-transform: uppercase;
    font-weight: 700;
    color: white;
    height: 200px;
}

.line-1{
    @media screen and (max-width: 500px) {
        display: flex;
        justify-content: center;
    }
}

.stat-img{
    width: 200px;
    height: 200px;
    display: flex;
    flex-wrap: wrap;
    padding: 0 10px;
    & > div{
        width: 100px;
        height: 100px;
    }
    img{
        margin: 0 2px;
        height: 96px;
        width: 96px;
    }
    p{
        text-align: center;
        text-transform: none;
    }
}

.stat-checkbox{
    display: none;
    &:checked{
        & ~ .pokemon-stats-background{
            display: initial;
        }
    }
}

.cross{
    position: absolute;
    top: -15px;
    right: -15px;
    height: 30px;
    width: 30px;
    border-radius: 50%;
    background: white;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    font-family: open-sans, sans-serif;
    font-weight: 700;
    font-size: 20px;
    text-transform: uppercase;
    color: #ff0100;
}

.stat-name{
    padding: 22px 10px;
    width: 250px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    p{
        padding: 2px 0;
    }
    @media screen and (max-width: 500px) {
        display: none;
    }
}

.stat-ability{
    width: 250px;
    padding: 22px 10px;
    & > p, & li{
        padding: 2px 0;
    }
    @media screen and (max-width: 500px) {
        display: none;
    }
}

.abilities{
    height: 78px;
    @include listStyle();
}

.line-2{
    height: 300px;
    padding: 0 10px;
}

.stat-move{
    width: 550px;
    & > p{
        padding: 4px 0;
    }
    @media screen and (max-width: 500px) {
        display: none;
    }
}

.stat-stat{
    width: 230px;
    & > p{
        padding: 4px 0;
    }
}

.moves, .stats{
    @include listStyle();
    height: 270px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}


.stat-line{
    height: 3px;
    margin: 5px 0;
}