.poke_div {
  margin: 10px;
  width: 236px;
  height: 280px;
  border-radius: 5px;
  position: relative;
  overflow: hidden;
}

.poke_div:hover {
  cursor: pointer;
}

.poke_div:hover > .wave {
  -webkit-animation: drift 4500ms infinite linear;
          animation: drift 4500ms infinite linear;
}

.poke_div:hover > .wave.-three {
  -webkit-animation: drift 7500ms infinite linear;
          animation: drift 7500ms infinite linear;
}

.poke_div:hover > .wave.-two {
  -webkit-animation: drift 10500ms infinite linear;
          animation: drift 10500ms infinite linear;
}

.poke_div:hover > .poke_text {
  color: #ff0100;
}

.poke_div:hover > .poke_img {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.wave {
  opacity: 0.4;
  position: absolute;
  top: 3%;
  left: 50%;
  width: 450px;
  height: 450px;
  margin-left: -225px;
  margin-top: -225px;
  -webkit-transform-origin: 50% 48%;
          transform-origin: 50% 48%;
  border-radius: 43%;
}

.wave:not(.-four) {
  background: #950101;
}

.wave.-two {
  opacity: 0.1;
  background: white;
}

.wave.-four {
  background: radial-gradient(at bottom, #ff0100, rgba(0, 0, 0, 0));
}

.poke_div:after {
  content: "";
  display: block;
  left: 0;
  top: 0;
  width: 236px;
  height: 280px;
  border-radius: 5px;
  z-index: 11;
}

.poke_img {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 200px;
  z-index: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.poke_img img {
  height: 200px;
  width: auto;
}

.poke_text {
  position: absolute;
  bottom: 5px;
  left: 50%;
  width: 100%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  color: white;
  font-family: open-sans, sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 18px;
}

.notFound {
