@import url("https://use.typekit.net/ufa2ftq.css");
.searchbar-conatiner{
    display: flex;
    justify-content: space-between;
    padding: 15px 0;
    @media screen and (max-width: 520px) {
        flex-direction: column;
        & > *{
            margin-bottom: 10px;
        }
    }
}

.logo{
    display: flex;
    align-items: center;
    img{
        height: 40px;
        width: auto;
        @media screen and (max-width: 580px) {
            height: 30px;
        }
    }
    p{
        padding: 0 10px;
        font-family: open-sans, sans-serif;
        font-weight: 800;
        font-style: normal;
        font-size: 30px;
        @media screen and (max-width: 580px) {
            font-size: 25px;
        }
    }
    @media screen and (max-width: 520px) {
        justify-content: center;
    }
}

.input{
    color: white;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    span{
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 20px;
        height: 40px;
        width: 40px;
        color: #ff0100;
        border: 1px solid #ff0100;
        border-right: none;
        border-radius: 50% 0 0 50%;
        @media screen and (max-width: 700px) {
            width: 30px;
            height: 30px;
        }
        @media screen and (max-width: 520px) {
            height: 40px;
            width: 40px;
        }
    }
    input{
        border: 1px solid #ff0100;
        border-left: none;
        border-radius: 0 20px 20px 0;
        background: none;
        outline: none;
        color: white;
        font-family: open-sans, sans-serif;
        font-weight: 500;
        letter-spacing: 0.5px;
        font-style: normal;
        font-size: 14px;
        height: 40px;
        width: 300px;
        caret-color: #950101;
        &:focus{
            border: 1px solid #950101;
            border-left: none;

            & ~ span {
                color: #950101;
                border: 1px solid #950101;
                border-right: none;
            }
        }
        @media screen and (max-width: 700px) {
            width: 200px;
            height: 30px;
        }
        @media screen and (max-width: 520px) {
            width: 250px;
            height: 40px;
        }
    }
    @media screen and (max-width: 520px) {
        transform: scale(0.8);
        justify-content: center;
    }
}

.white{
    color: white;
}

.red{
    color: #ff0100;
}